// app/(promos)/promociones/page.tsx
"use client";

import { useEffect, useState } from "react";
import { Promo, Producto, Relacion } from "./types";
import PromoHeader from "./components/promoHeader";
import PromoContainer from "./components/promoContainer";

export default function PromocionesPage() {
    const [promos, setPromos] = useState<Promo[]>([]);
    const [productos, setProductos] = useState<Producto[]>([]);
    const [rel, setRel] = useState<Relacion[]>([]);
    const [loading, setLoading] = useState(true);

    useEffect(() => {
        setPromos(JSON.parse(localStorage.getItem("kwikemart_promos") || "[]"));
        setProductos(
            JSON.parse(localStorage.getItem("kwikemart_productos") || "[]")
        );
        setRel(
            JSON.parse(
                localStorage.getItem("kwikemart_promo_productos") || "[]"
            )
        );
        setLoading(false);
    }, []);

    const dia = new Date().getDay();
    const promosHoy = promos.filter((p) => p.diasSemana.includes(dia));

    return (
        <main className="min-h-screen bg-gradient-to-b from-yellow-100 to-orange-100 dark:from-gray-900 dark:to-gray-800 py-10 px-4">
            <PromoHeader dia={dia} />
            <PromoContainer
                loading={loading}
                promosHoy={promosHoy}
                productos={productos}
                rel={rel}
                dia={dia}
            />
        </main>
    );
}
