// app/(promos)/promociones/components/PromoContainer.tsx
import Image from "next/image";
import { Promo, Producto, Relacion } from "../types";
import PromoCard from "./promoCard";

interface Props {
    loading: boolean;
    promosHoy: Promo[];
    productos: Producto[];
    rel: Relacion[];
    dia: number;
}

export default function PromoContainer({
    loading,
    promosHoy,
    productos,
    rel,
}: Props) {
    const getProductosPromo = (promoId: number) => {
        const ids = rel
            .filter((r) => r.idPromo === promoId)
            .map((r) => r.idProducto);
        return productos.filter((p) => ids.includes(p.id));
    };

    if (loading) {
        return (
            <div className="flex flex-col items-center mt-20">
                <span className="text-xl text-gray-500">
                    Cargando promociones‚Ä¶
                </span>
            </div>
        );
    }

    if (promosHoy.length === 0) {
        return (
            <div className="flex flex-col items-center mt-24">
                <Image
                    src="/images/apu-triste.png"
                    alt="Apu triste"
                    width={160}
                    height={160}
                    className="mb-4"
                />
                <h2 className="text-2xl font-simpson text-gray-700 dark:text-gray-200">
                    ¬°Hoy no hay promociones activas!
                </h2>
                <p className="text-lg text-gray-600 dark:text-gray-400 mt-2">
                    ‚ÄúMuchas gracias, vuelva pronto‚Ä¶‚Äù
                </p>
            </div>
        );
    }

    return (
        <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-2 xl:grid-cols-3 gap-8 max-w-6xl mx-auto">
            {promosHoy.map((promo) => (
                <PromoCard
                    key={promo.id}
                    promo={promo}
                    productos={getProductosPromo(promo.id)}
                />
            ))}
        </div>
    );
}
